<template>
  <div class="tags">
    <ul>
      <li
        class="tags-li"
        v-for="(item,index) in tagsList"
        :class="{'active': isActive(item.path)}"
        :key="index"
      >
        <router-link :to="item.path" class="tags-title">{{item.title}}</router-link>
        <span class="tags-li-icon" @click="closeTag(index)">
          <i class="el-icon-close"></i>
        </span>
      </li>
    </ul>
    <div class="tags-close-box">
      <el-dropdown @command="handleTags">
        <el-button size="mini" type="primary">
          标签选项
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu size="small" slot="dropdown">
          <el-dropdown-item command="other">关闭其他</el-dropdown-item>
          <el-dropdown-item command="all">关闭所有</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import bus from "@/components/common/bus";
export default {
  data() {
    return {
      tagsList: []
    };
  },
  methods: {
    isActive(path) {
      return path === this.$route.fullPath;
    },
    // 设置标签
    setTags() {
      // 是否已经存在于列表
      const isExist = this.tagsList.some(item => {
        return item.path == this.$route.fullPath;
      });
      if (!isExist) {
        this.tagsList.push({
          title: this.$route.meta.title,
          path: this.$route.fullPath,
          name: this.$route.matched[1].name
        });
      }
      bus.$emit("tags", this.tagsList);
    },
    // 关闭单个标签
    closeTag(index) {
      const delItem = this.tagsList.splice(index, 1)[0];
      const item = this.tagsList[index]
        ? this.tagsList[index] //说明关闭的不是最后一项
        : this.tagsList[index - 1]; //关闭的是最后一项
      //item不存在说明标签关闭完成
      if (item) {
        //删除相和当前路由一致时，路由跳转
        delItem.path === this.$route.fullPath && this.$router.push(item.path);
      } else {
        this.$router.push("/");
      }
    },
    // 删除所有标签
    closeAll() {
      this.tagsList = [];
      this.$router.push("/");
    },
    // 关闭其他，除当前
    closeOther() {
      const courTag = this.tagsList.filter(item => {
        return item.path === this.$route.fullPath;
      });
      this.tagsList = courTag;
    },
    handleTags(command) {
      command === "other" ? this.closeOther() : this.closeAll();
    }
  },
  watch: {
    $route(newValue, oldValue) {
      this.setTags();
    }
  },
  created() {
    this.setTags();
  }
};
</script>

<style>
.tags {
  display: flex;
  height: 30px;
  overflow: hidden;
  background: #fff;
}
.tags ul {
  flex: 1;
  box-sizing: border-box;
}
.tags-li {
  float: left;
  margin: 3px 5px 2px 3px;
  border-radius: 3px;
  font-size: 12px;
  overflow: hidden;
  cursor: pointer;
  height: 23px;
  line-height: 23px;
  border: 1px solid #e9eaec;
  background: #fff;
  padding: 0 5px 0 12px;
  vertical-align: middle;
  color: #666;
  transition: all 0.3s ease-in;
}
.tags-li:not(.active):hover {
  background: #f8f8f8;
}

.tags-li.active {
  color: #fff;
}
.tags-title {
  float: left;
  max-width: 80px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 5px;
  color: #666;
}

.tags-li.active .tags-title {
  color: #fff;
}
.tags-close-box {
  box-sizing: border-box;
  padding-top: 1px;
  text-align: center;
  width: 110px;
  height: 30px;
  background: #fff;
  box-shadow: -3px 0 15px 3px rgba(0, 0, 0, 0.1);
  z-index: 10;
}
</style>
